@import url('https://fonts.googleapis.com/css2?family=Kadwa:wght@400;700&display=swap');

body {
  margin: 0;
  font-family: 'Kadwa', serif;
  background-color: #f9f9f9;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #2d4300;
  color: white;
  padding: 1rem;
  position: relative;
}

.logo {
  font-weight: 700;
  font-size: 1.5rem;
}

.menu-toggle {
  font-size: 1.5rem;
  cursor: pointer;
  display: none;
}

/* Navigation-Links */
.nav {
  display: flex;
  gap: 1rem;
}

.nav a {
  color: white;
  text-decoration: none;
  font-weight: 400;
}

/* Mobile Styles */
@media (max-width: 600px) {
  .menu-toggle {
    display: block;
  }

  .nav {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 100px;
    right: 0;
    background-color: #333;
    width: 100%;
    padding: 1rem 0;
    z-index: 1000;
  }

  .nav.open {
    display: flex;
  }

  .nav a {
    padding: 0.75rem 1rem;
    border-top: 1px solid #444;
  }
}

.main-content {
  padding: 2rem;
}
