h2{
  margin-top: 0;
  font-size: small;
  margin-bottom: -15px;
  font-weight: lighter;
  font-style: italic;
}

body {
  margin: 0;
  font-family: 'Kadwa', serif;
  background-color: #f9f9f9;
}

.settings-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 60px); /* Platz für Header */
}

.settings-box {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  padding: 2rem;
  background: white;
  border-radius: 8px;
  border: 1px solid #ccc;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  font-family: 'Kadwa', serif;
  min-width: 280px;
}

.settings-box input,
.settings-box button {
  font-family: inherit;
  padding: 0.5rem;
  font-size: 1rem;
}

.settings-box button {
  background-color: #333;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}


body {
  margin: 0;
  font-family: 'Kadwa', serif;
  background-color: #f9f9f9;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #2d4300;
  color: white;
  padding: 1rem;
  position: relative;
}

.logo {
  font-weight: 700;
  font-size: 1.5rem;
}

.menu-toggle {
  font-size: 1.5rem;
  cursor: pointer;
  display: none;
}

/* Navigation-Links */
.nav {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.nav a {
  color: white;
  text-decoration: none;
  font-weight: 400;
}

/* Mobile Styles */
@media (max-width: 600px) {
  .menu-toggle {
    display: block;
  }

  .nav {
    display: none;
    flex-direction: column;
    position: absolute;
    top: 100px;
    right: 0;
    background-color: #333;
    width: 100%;
    padding: 1rem 0;
    z-index: 1000;
  }

  .nav.open {
    display: flex;
  }

  .nav a {
    padding: 0.75rem 1rem;
    border-top: 1px solid #444;
  }
}

.main-content {
  padding: 2rem;
}

